    "remove_all_clones_exampleDesc": "[시작하기]버튼을 클릭하면 자신의 복제본 5개를 만듭니다. 복제본이 새로 생성되었을때 복제본의 x좌표를 -200에서 200사이의 무작위수로 정합니다. 스페이스 키를 누르면 모든 복제본을 삭제합니다.",
    "move_direction_desc": "A만큼 오브젝트의 이동방향 화살표가 가리키는 방향으로 움직입니다.",
