    "locate_x_exampleDesc": "오른쪽화살표키를 누르면 오브젝트의 x좌표를 100으로 정하고, 왼쪽화살표키를 누르면 오브젝트의 x좌표를 -100으로 정합니다.",
    "locate_y_desc": "오브젝트의 y좌표를 A로 정합니다. (오브젝트의 중심점이 기준이 됩니다.)",
