    "get_today_temperature_exampleCode": "def when_start():\n    Entry.print(Weather.get_today_temperature(\"Seoul\", 00))",
    "get_today_temperature_exampleDesc": "[시작하기]버튼을 클릭하면 오브젝트가 오늘 서울의 00시 기온을 말합니다.​",
